export default {
	data() {
		return {
			news : []
		}
	},
	onLoad:function() {
		uni.showLoading({
			title:"加载中 ..."
		})
		uni.request({
			url: 'https://unidemo.dcloud.net.cn/api/news',
			method: 'GET',
			data: {},
			success: res => {
				console.log(res);
				this.news = res.data;
				uni.hideLoading();
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods: {
		openDetail(e){
			console.log(e);
			var newsId = e.currentTarget.dataset.newsid;
			uni.navigateTo({
				url: '../news_detali/news_detali?newsId='+newsId
			});
		}
	}
}
